___CSS_LOADER_EXPORT___.push([module.id, `.switchContainer {
    display: flex;
    justify-content: center;
    align-items: center;
    bottom: 20px;
    right: 20px;
    padding: 5px 20px;
    cursor: pointer;
}

.switchContainer p {
    margin-right: 10px;
    font-size: 1rem;
}

.switch {
    position: relative;
    display: inline-block;
    width: 40px;
    height: 22px;
}

.switch input {
    opacity: 0;
    width: 0;
    height: 0;
}

.switchSlider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    transition: .4s;
    border-radius: 22px;
}

.switchSlider:before {
    position: absolute;
    content: "";
    height: 18px;
    width: 18px;
    left: 2px;
    bottom: 2px;
    background-color: white;
    transition: .4s;
    border-radius: 50%;
}

input:checked+.switchSlider {
    background-color: #002a6e;
}

input:checked+.switchSlider:before {
    transform: translateX(18px);
}`, "",{"version":3,"sources":["webpack://./src/css/switch.css"],"names":[],"mappings":"AAAA;IACI,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,YAAY;IACZ,WAAW;IACX,iBAAiB;IACjB,eAAe;AACnB;;AAEA;IACI,kBAAkB;IAClB,eAAe;AACnB;;AAEA;IACI,kBAAkB;IAClB,qBAAqB;IACrB,WAAW;IACX,YAAY;AAChB;;AAEA;IACI,UAAU;IACV,QAAQ;IACR,SAAS;AACb;;AAEA;IACI,kBAAkB;IAClB,eAAe;IACf,MAAM;IACN,OAAO;IACP,QAAQ;IACR,SAAS;IACT,sBAAsB;IACtB,eAAe;IACf,mBAAmB;AACvB;;AAEA;IACI,kBAAkB;IAClB,WAAW;IACX,YAAY;IACZ,WAAW;IACX,SAAS;IACT,WAAW;IACX,uBAAuB;IACvB,eAAe;IACf,kBAAkB;AACtB;;AAEA;IACI,yBAAyB;AAC7B;;AAEA;IACI,2BAA2B;AAC/B","sourcesContent":[".switchContainer {\r\n    display: flex;\r\n    justify-content: center;\r\n    align-items: center;\r\n    bottom: 20px;\r\n    right: 20px;\r\n    padding: 5px 20px;\r\n    cursor: pointer;\r\n}\r\n\r\n.switchContainer p {\r\n    margin-right: 10px;\r\n    font-size: 1rem;\r\n}\r\n\r\n.switch {\r\n    position: relative;\r\n    display: inline-block;\r\n    width: 40px;\r\n    height: 22px;\r\n}\r\n\r\n.switch input {\r\n    opacity: 0;\r\n    width: 0;\r\n    height: 0;\r\n}\r\n\r\n.switchSlider {\r\n    position: absolute;\r\n    cursor: pointer;\r\n    top: 0;\r\n    left: 0;\r\n    right: 0;\r\n    bottom: 0;\r\n    background-color: #ccc;\r\n    transition: .4s;\r\n    border-radius: 22px;\r\n}\r\n\r\n.switchSlider:before {\r\n    position: absolute;\r\n    content: \"\";\r\n    height: 18px;\r\n    width: 18px;\r\n    left: 2px;\r\n    bottom: 2px;\r\n    background-color: white;\r\n    transition: .4s;\r\n    border-radius: 50%;\r\n}\r\n\r\ninput:checked+.switchSlider {\r\n    background-color: #002a6e;\r\n}\r\n\r\ninput:checked+.switchSlider:before {\r\n    transform: translateX(18px);\r\n}"],"sourceRoot":""}]);
