    --element-border-color: #555;
    --link-color: #4baeff;
}

/**************************************/

.marginTopHigh {
    margin-top: 20px;
}

.marginTopMedium {
    margin-top: 10px;
}

.marginTopLow {
    margin-top: 5px;
}

.marginLeftLow {
    margin-left: 5px;
}

.marginRightLow {
