.justifyCenter {
    justify-content: center;
}

.justifySpaceBetween {
    justify-content: space-between;
}

.gapLow {
    gap: 5px;
}

.alignCenter {
    align-items: center;
}

.flexColumn {
    flex-direction: column;
}

.flexRow {
    flex-direction: row;
}

.positionRelative {
    position: relative;
}

.textAlignCenter {
    text-align: center;
}

/**************************************/

.paddingLow {
    padding: 5px;
}

.paddingHigh {
    padding: 15px;
}

.displayNone {
    display: none;
}

.displayBlock {
    display: block;
}

.displayFlex {
    display: flex;
}

.fontLarge {
    font-size: var(--large-font-size);
}

.fontMedium {
    font-size: var(--medium-font-size);
}

.fontSmall {
    font-size: var(--small-font-size);
}

.textDecorationNone {
    text-decoration: none;
}

.borferNone {
    border: none;
}

