    border-radius: 10px;
}

.borderRadiusExtreme {
    border-radius: 20px;
}

.borderRadiusOnlyTop {
    border-radius: 10px 10px 0 0;
}

.borderRadiusOnlyBottom {
    border-radius: 0 0 15px 15px;
}

.borderSolidNone {
    border: 0px, solid;
}

.borderCollapse {
    border-collapse: collapse;
}

.frontColor {
    color: var(--front-color);
}

.frontLinkColor {
    color: var(--link-color);
}

