___CSS_LOADER_EXPORT___.push([module.id, `* {
    user-select: none;
    transition: background-color 0.3s ease-in-out;
}

.inputReact {
    background-color: var(--element-backgound-color);
    color: var(--front-color);
    padding: 5px;
    border: 1px solid var(--element-border-color);
    border-radius: 5px;
    font-size: var(--medium-font-size);
}

input[type=file] {
    display: none;
}

input[type=number] {
    width: 60px;
}

Canvas {
    border: 1px solid var(--element-border-color);
}

/**********************************************/

.title {
    font-size: clamp(30px, 5vw, 50px);
    margin-bottom: 10px;
}

.subTitle {
    font-size: clamp(20px, 3vw, 30px);
    margin-top: auto;
    margin-bottom: 12px;
}

.PageContainer {
    max-width: 1000px;
    min-width: 300px;
}

/**********************************************/

#decodeInputCanvas,
#decodeOutputCanvas,
#hiddenCanvas,
#encodeOutputCanvas {
    max-width: min(90%, 600px);
    margin-left: auto;
    margin-right: auto;
}

#decodeUrlInput input[type=text] {
    width: 100px;
}

/**********************************************/

.encodeSlider input[type=range] {
    max-width: 320px;
}

/**********************************************/

#privacyPolicy {
    max-width: min(100%, 600px);
    margin-top: 10px;
    margin-bottom: 10px;
}

#versionRecordTable {
    border-collapse: collapse;
    max-width: min(100%, 600px);
}

#versionRecordTable td {
    padding: 10px;
