___CSS_LOADER_EXPORT___.push([module.id, `:root {
    --sidebar-width: 200px;
    --sidebar-padding: 20px;
}

.sidebarContainer {
    position: fixed;
    top: 0;
    right: 0;
    width: var(--sidebar-width);
    height: 100%;
    background-color: var(--secondary-background-color);
    border-left: 5px solid var(--element-backgound-color);
    box-shadow: -5px 0 10px rgba(0, 0, 0, 0.1);
    padding: var(--sidebar-padding);
    z-index: 100;
    transition: transform 0.5s;
    overflow-y: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.sidebarHide {
    transform: translateX(calc(var(--sidebar-width) + var(--sidebar-padding)));
}

.sidebarShow {
    transform: translateX(0);
}

.sidebarContent {
    width: 100%;
    overflow-y: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.sidebarContent Canvas {
    width: 90%;
    margin-top: 20px;
}

.sidebarToggle {
    position: absolute;
    top: 50%;
    left: 0px;
    box-shadow: -5px 0 10px rgba(0, 0, 0, 0.1);
    transform: translateY(-50%);
    transform: translateX(-50%);
    width: 40px;
    height: 40px;
    background-color: var(--element-backgound-color);
    color: var(--front-color);
    font-size: var(--large-font-size);
    border: 0px solid;
    border-radius: 50%;
    cursor: pointer;
    z-index: 101;
}`, "",{"version":3,"sources":["webpack://./src/css/sidebar.css"],"names":[],"mappings":"AAAA;IACI,sBAAsB;IACtB,uBAAuB;AAC3B;;AAEA;IACI,eAAe;IACf,MAAM;IACN,QAAQ;IACR,2BAA2B;IAC3B,YAAY;IACZ,mDAAmD;IACnD,qDAAqD;IACrD,0CAA0C;IAC1C,+BAA+B;IAC/B,YAAY;IACZ,0BAA0B;IAC1B,gBAAgB;IAChB,aAAa;IACb,sBAAsB;IACtB,mBAAmB;AACvB;;AAEA;IACI,0EAA0E;AAC9E;;AAEA;IACI,wBAAwB;AAC5B;;AAEA;IACI,WAAW;IACX,gBAAgB;IAChB,aAAa;IACb,sBAAsB;IACtB,mBAAmB;AACvB;;AAEA;IACI,UAAU;IACV,gBAAgB;AACpB;;AAEA;IACI,kBAAkB;IAClB,QAAQ;IACR,SAAS;IACT,0CAA0C;IAC1C,2BAA2B;IAC3B,2BAA2B;IAC3B,WAAW;IACX,YAAY;IACZ,gDAAgD;IAChD,yBAAyB;IACzB,iCAAiC;IACjC,iBAAiB;IACjB,kBAAkB;IAClB,eAAe;IACf,YAAY;AAChB","sourcesContent":[":root {\r\n    --sidebar-width: 200px;\r\n    --sidebar-padding: 20px;\r\n}\r\n\r\n.sidebarContainer {\r\n    position: fixed;\r\n    top: 0;\r\n    right: 0;\r\n    width: var(--sidebar-width);\r\n    height: 100%;\r\n    background-color: var(--secondary-background-color);\r\n    border-left: 5px solid var(--element-backgound-color);\r\n    box-shadow: -5px 0 10px rgba(0, 0, 0, 0.1);\r\n    padding: var(--sidebar-padding);\r\n    z-index: 100;\r\n    transition: transform 0.5s;\r\n    overflow-y: auto;\r\n    display: flex;\r\n    flex-direction: column;\r\n    align-items: center;\r\n}\r\n\r\n.sidebarHide {\r\n    transform: translateX(calc(var(--sidebar-width) + var(--sidebar-padding)));\r\n}\r\n\r\n.sidebarShow {\r\n    transform: translateX(0);\r\n}\r\n\r\n.sidebarContent {\r\n    width: 100%;\r\n    overflow-y: auto;\r\n    display: flex;\r\n    flex-direction: column;\r\n    align-items: center;\r\n}\r\n\r\n.sidebarContent Canvas {\r\n    width: 90%;\r\n    margin-top: 20px;\r\n}\r\n\r\n.sidebarToggle {\r\n    position: absolute;\r\n    top: 50%;\r\n    left: 0px;\r\n    box-shadow: -5px 0 10px rgba(0, 0, 0, 0.1);\r\n    transform: translateY(-50%);\r\n    transform: translateX(-50%);\r\n    width: 40px;\r\n    height: 40px;\r\n    background-color: var(--element-backgound-color);\r\n    color: va